import {uniRate} from "@dcloudio/uni-ui"
export default {
	components: {
		uniRate
	},
	props: [
		'rateItem'
	],
	data() {
		return {}
	},
	methods: {
		showImage(event) {
			let photo = event.currentTarget.dataset.photo
			uni.previewImage({
				urls: [photo]
			})
		}
	}
}
